import React from 'react';
import {Modal, Form, Col, Button, Media} from "react-bootstrap";
import Avatar from '../assets/img/avatar-red.svg';

const DataRequired =({dataRequiredSelected, handleChange, editDataRequired, show, setShow})=> {
    return (
        <Modal
            show={show}
            onHide={() => setShow(false)}
            dialogClassName="modal-60w"
            backdrop="static"
            keyboard={false}
            animation={false}
        >
            <Modal.Header className="border-0">
                <h3 className="mb-0">Data required</h3>
            </Modal.Header>
            <Modal.Body>
                <Form>

                    <Form.Group as={Col} className="p-0" controlId="formFirstName">
                        <Form.Label>Avatar</Form.Label>
                        <Media>
                            <div className="container-avatar mt-2 mb-3 mr-3"><img src={Avatar} width={164} alt="avatar"/></div>
                            <Media.Body>
                                <button className="btn btn-light border mt-4">Change</button>
                            </Media.Body>
                        </Media>
                    </Form.Group>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formFirstName">
                            <Form.Label>First name</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                name="firstName"
                                value={dataRequiredSelected && dataRequiredSelected.firstName}
                                onChange={(e)=>handleChange(e)}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formLastName">
                            <Form.Label>Last name</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                name="lastName"
                                value={dataRequiredSelected && dataRequiredSelected.lastName}
                                onChange={(e)=>handleChange(e)}
                            />
                        </Form.Group>
                    </Form.Row>

                    <Form.Group controlId="formRole">
                        <Form.Label>Role</Form.Label>
                        <Form.Control
                            as="select"
                            name="role"
                            value={dataRequiredSelected && dataRequiredSelected.role}
                            onChange={(e)=>handleChange(e)}
                            custom
                        >
                            <option value="" disabled="">Choose...</option>
                            <option>Backend Engineer</option>
                            <option>Business Analyst</option>
                            <option>Dev Ops</option>
                            <option>Frontend Engineer</option>
                            <option>Fullstack Engineer</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="formSeniority">
                        <Form.Label>Seniority</Form.Label>
                        <Form.Control
                            as="select"
                            name="level"
                            value={dataRequiredSelected && dataRequiredSelected.level}
                            onChange={(e)=>handleChange(e)}
                            custom
                        >
                            <option value="" disabled="">Choose...</option>
                            <option>Mid Level</option>
                            <option>Senior</option>
                            <option>Tech Lead</option>
                        </Form.Control>
                    </Form.Group>

                    <ul className="list-inline mt-4 float-right">
                        <li className="list-inline-item">
                            <Button variant="link" onClick={()=>setShow(false)}>
                                Cancel
                            </Button>
                        </li>
                        <li className="list-inline-item">
                            <Button variant="primary px-4" onClick={()=>editDataRequired()}>
                                Save
                            </Button>
                        </li>
                    </ul>
                </Form>
            </Modal.Body>
        </Modal>
    );
}

export default DataRequired;