import React from 'react';
import {Modal, Form, Col, Button, Row} from "react-bootstrap";

const CertificationAdd=({certificationSelected, handleChange, addCertification, show, setShow})=> {
    return (
        <Modal
            show={show}
            onHide={() => setShow(false)}
            dialogClassName="modal-60w"
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header className="border-0">
                <h3 className="mb-0">New Certification</h3>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Row className="mb-3">
                        <Col xs={6}>
                            <Form.Label>Start date</Form.Label>
                            <Form.Control
                                type="date"
                                placeholder=""
                                name="startCerDate"
                                value={certificationSelected ? certificationSelected.startCerDate:''}
                                onChange={(e)=>handleChange(e)}
                            />
                        </Col>
                        <Col xs={6}>
                            <Form.Label>End date</Form.Label>
                            <Form.Control
                                type="date"
                                placeholder=""
                                name="endCerDate"
                                value={certificationSelected ? certificationSelected.endCerDate:''}
                                onChange={(e)=>handleChange(e)}
                            />
                        </Col>
                    </Row>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Institute</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder=""
                            name="school"
                            value={certificationSelected ? certificationSelected.school:''}
                            onChange={(e)=>handleChange(e)}
                        />
                    </Form.Group>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Degree</Form.Label>
                        <Form.Control
                            as="select"
                            name="degree"
                            value={certificationSelected ? certificationSelected.degree:''}
                            onChange={(e)=>handleChange(e)}
                            custom
                        >
                            <option value="" disabled="">Choose...</option>
                            <option>Barchelor Applied Science</option>
                            <option>Barchelor of Business Administration</option>
                            <option>Barchelor of design</option>
                        </Form.Control>
                    </Form.Group>



                    <ul className="list-inline mt-4 float-right">
                        <li className="list-inline-item">
                            <Button variant="link" onClick={()=>setShow(false)}>
                                Cancel
                            </Button>
                        </li>
                        <li className="list-inline-item">
                            <Button variant="primary px-4" onClick={()=>addCertification()}>
                                Save
                            </Button>
                        </li>
                    </ul>
                </Form>
            </Modal.Body>
        </Modal>
    );
}

export default CertificationAdd;