import React from 'react';
import {Modal, Form, Col, Button, Row} from "react-bootstrap";
import TextareaAutosize from 'react-textarea-autosize';

const SoftSkillAdd=({softSkillSelected, handleChange, addSoftSkill, show, setShow})=> {
    return (
        <Modal
            show={show}
            onHide={() => setShow(false)}
            dialogClassName="modal-60w"
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header className="border-0">
                <h3 className="mb-0">New SoftSkill</h3>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Skill</Form.Label>
                        <Form.Control
                            as="select"
                            name="skill"
                            value={softSkillSelected ? softSkillSelected.skill:''}
                            onChange={(e)=>handleChange(e)}
                            custom
                        >
                            <option value="" disabled="">Choose...</option>
                            <option>Proactivity</option>
                            <option>Innovation</option>
                            <option>Teamwork</option>
                        </Form.Control>
                    </Form.Group>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Recommendated by</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder=""
                            name="name"
                            value={softSkillSelected ? softSkillSelected.name:''}
                            onChange={(e)=>handleChange(e)}
                        />
                    </Form.Group>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Position</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder=""
                            name="position"
                            value={softSkillSelected ? softSkillSelected.position:''}
                            onChange={(e)=>handleChange(e)}
                        />
                    </Form.Group>
                    <Form.Group controlId="formChallenge">
                        <Form.Label>Quote</Form.Label>
                        <TextareaAutosize
                            cacheMeasurements
                            name="quote"
                            value={softSkillSelected?softSkillSelected.quote:''}
                            onChange={(e)=>handleChange(e)}
                            className="form-control text-area-autosize"
                        />
                    </Form.Group>


                    <ul className="list-inline mt-4 float-right">
                        <li className="list-inline-item">
                            <Button variant="link" onClick={()=>setShow(false)}>
                                Cancel
                            </Button>
                        </li>
                        <li className="list-inline-item">
                            <Button variant="primary px-4" onClick={()=>addSoftSkill()}>
                                Save
                            </Button>
                        </li>
                    </ul>
                </Form>
            </Modal.Body>
        </Modal>
    );
}

export default SoftSkillAdd;