import React from 'react';
import {Modal, Form, Col, Button, Row} from "react-bootstrap";

const EnglishCertificationEdit=({englishCertificationSelected, handleChange, editEnglishCertifications, show, setShow})=> {
    return (
        <Modal
            show={show}
            onHide={() => setShow(false)}
            dialogClassName="modal-60w"
            backdrop="static"
            keyboard={false}
            animation={false}
        >
            <Modal.Header className="border-0">
                <h3 className="mb-0">Edit English Certifications</h3>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Row>
                        <Col xs={4}>
                            <Form.Label>Year</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                name="year"
                                value={englishCertificationSelected && englishCertificationSelected.year}
                                onChange={(e)=>handleChange(e)}
                            />
                        </Col>
                    </Row>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Institute</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder=""
                            name="institute"
                            value={englishCertificationSelected && englishCertificationSelected.institute}
                            onChange={(e)=>handleChange(e)}
                        />
                    </Form.Group>
                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Certification</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder=""
                            name="certification"
                            value={englishCertificationSelected && englishCertificationSelected.certification}
                            onChange={(e)=>handleChange(e)}
                        />
                    </Form.Group>

                    <ul className="list-inline mt-4 float-right">
                        <li className="list-inline-item">
                            <Button variant="link" onClick={()=>setShow(false)}>
                                Cancel
                            </Button>
                        </li>
                        <li className="list-inline-item">
                            <Button variant="primary px-4" onClick={()=>editEnglishCertifications()}>
                                Save
                            </Button>
                        </li>
                    </ul>
                </Form>
            </Modal.Body>
        </Modal>
    );
}

export default EnglishCertificationEdit;